.card {
  width: 200px;
  height: 350px;
  border-radius: 10px;
  border-right: 1px solid rgba(50, 50, 50, 0.4);
  border-bottom: 1px solid rgba(50, 50, 50, 0.4);
  background-color: white;
  font-size: 26px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out; }
  .card.hidden {
    background: #00C9FF;
    /* fallback for old browsers */
    background: -webkit-radial-gradient(center, circle, #92FE9D, #00C9FF);
    background: -o-radial-gradient(center, circle, #92FE9D, #00C9FF);
    background: radial-gradient(circle at center, #92FE9D, #00C9FF);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }
  .card img {
    width: 60px;
    height: 60px;
    display: block;
    pointer-events: none; }
  .card span {
    padding: 10px; }
  .card .top-left {
    position: absolute;
    top: 15px;
    left: 15px;
    text-align: left; }
  .card .bottom-right {
    position: absolute;
    bottom: 15px;
    right: 15px;
    text-align: right; }



