body {
  margin: 0;
  padding: 30px;
  background: rgba(174, 194, 210, 0.75);
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #eef2f3, rgba(174, 194, 210, 0.75));
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#eef2f3), to(rgba(174, 194, 210, 0.75)));
  background: -webkit-linear-gradient(left, #eef2f3, rgba(174, 194, 210, 0.75));
  background: -o-linear-gradient(left, #eef2f3, rgba(174, 194, 210, 0.75));
  background: linear-gradient(to right, #eef2f3, rgba(174, 194, 210, 0.75));
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: rgba(0, 0, 0, 0.86);
  font-family: 'Open Sans', sans-serif; }

h1, h2, h3, h4 {
  margin-top: 5px;
  color: rgba(0, 0, 0, 0.7);
  font-family: 'Permanent Marker', cursive; }

h1 {
  font-size: 48px; }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }



